import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';



class ContainerItem extends React.Component {
  render() {
    return(
      <div className='singleItem'>
        {this.props.text}
      </div>
    );
  }
}

class Container extends React.Component {
  constructor() {
    super();
    this.state = {
      text: '',
      items: [],
      infoEnd: 'zadań'
    }
  }
  render() {
    
    return(
      <div id='main'>
        <h1 className='header'>Lista zadań na dziś</h1>
        <p id='info'>W sumie <b>{this.state.items.length}</b> {this.state.infoEnd}.</p>
        {this.state.items.map((i) => <ContainerItem text={i}/>)}
        <div id='inputs'>
          <textarea onInput={this.autoGrow.bind(this)} spellCheck='false' onChange={this.textChange.bind(this)} value={this.state.text} onKeyPress={this.addOnEnter.bind(this)}></textarea>
          <button onClick={this.add.bind(this)}>Dodaj</button>
        </div>
      </div>
    );
  }
  addOnEnter(e) {
    if(e.key === 'Enter') {
      e.preventDefault()
      if(this.state.text) {
        let tab = this.state.items
        tab.push(this.state.text)
        this.setState({text: ''})
        if(this.state.items.length === 1) this.setState({infoEnd: 'zadanie'})
        else if(this.state.items.length > 1 && this.state.items.length < 5) this.setState({infoEnd: 'zadania'})
        else this.setState({infoEnd: 'zadań'})
      }
    }
  }
  add() {
    if(this.state.text) {
      let tab = this.state.items
      tab.push(this.state.text)
      this.setState({text: ''})
      if(this.state.items.length === 1) this.setState({infoEnd: 'zadanie'})
      else if(this.state.items.length > 1 && this.state.items.length < 5) this.setState({infoEnd: 'zadania'})
      else this.setState({infoEnd: 'zadań'})
    }
  }
  textChange(e) {
    this.setState({text: e.target.value})
  }
  autoGrow(e) {
    e.target.style.height = '5px'
    e.target.style.height = (e.target.scrollHeight)+'px';
  }
}

ReactDOM.render(
  <Container />,
  document.getElementById('root')
);


